import Vue from 'vue'
import VueRouter from 'vue-router'
import firebase from 'firebase/app'

import HomeView from '../views/HomeView.vue'
import customerList from '../views/customer/CustomerList.vue'
import customerUpdate from '../views/customer/CustomerUpdate.vue'
import recipeList from '../views/recipe/RecipeList.vue'
import recipeCreate from '../views/recipe/RecipeCreate.vue'
import RecipeLogin from '../views/RecipeLogin.vue'
import RecipeSignUp from '../views/RecipeSignUp.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/customer/list',
    name: 'customerList',
    component: customerList
  },
  {
    path: '/customer/update',
    name: 'customerUpdate',
    component: customerUpdate
  },
  {
    path: '/recipe/list',
    name: 'recipeList',
    component: recipeList
  },
  {
    path: '/recipe/create',
    name: 'recipeCreate',
    component: recipeCreate
  },
  {
    path: '/login',
    name: 'RecipeLogin',
    component: RecipeLogin
  },
  {
    path: '/signup',
    name: 'RecipeSignUp',
    component: RecipeSignUp
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// ========================================================================
// 画面遷移前処理
// ========================================================================
router.beforeEach((to, from, next) => {

console.log(firebase.auth().currentUser)

  if (to.path == '/login') {
    next()
    return
  }

  // 未ログインの場合
  if (!firebase.auth().currentUser) {
    next({
      path: '/login',
      query: { redirect: to.fullPath }
    })
    return
  }
  next()
})

export default router
